.dot-spinner {
  --uib-size: 8.8rem;
  --uib-speed: .9s;
  --uib-color: red;
  height: var(--uib-size);
  width: var(--uib-size);
  justify-content: flex-start;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dot-spinner__dot {
  height: 100%;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.dot-spinner__dot:before {
  content: "";
  height: 20%;
  width: 20%;
  background-color: var(--uib-color);
  opacity: .5;
  animation: pulse0112 calc(var(--uib-speed) * 1.111) ease-in-out infinite;
  border-radius: 50%;
  transform: scale(0);
  box-shadow: 0 0 20px #121f354d;
}

.dot-spinner__dot:nth-child(2) {
  transform: rotate(45deg);
}

.dot-spinner__dot:nth-child(2):before {
  animation-delay: calc(var(--uib-speed) * -.875);
}

.dot-spinner__dot:nth-child(3) {
  transform: rotate(90deg);
}

.dot-spinner__dot:nth-child(3):before {
  animation-delay: calc(var(--uib-speed) * -.75);
}

.dot-spinner__dot:nth-child(4) {
  transform: rotate(135deg);
}

.dot-spinner__dot:nth-child(4):before {
  animation-delay: calc(var(--uib-speed) * -.625);
}

.dot-spinner__dot:nth-child(5) {
  transform: rotate(180deg);
}

.dot-spinner__dot:nth-child(5):before {
  animation-delay: calc(var(--uib-speed) * -.5);
}

.dot-spinner__dot:nth-child(6) {
  transform: rotate(225deg);
}

.dot-spinner__dot:nth-child(6):before {
  animation-delay: calc(var(--uib-speed) * -.375);
}

.dot-spinner__dot:nth-child(7) {
  transform: rotate(270deg);
}

.dot-spinner__dot:nth-child(7):before {
  animation-delay: calc(var(--uib-speed) * -.25);
}

.dot-spinner__dot:nth-child(8) {
  transform: rotate(315deg);
}

.dot-spinner__dot:nth-child(8):before {
  animation-delay: calc(var(--uib-speed) * -.125);
}

@keyframes pulse0112 {
  0%, 100% {
    opacity: .5;
    transform: scale(0);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }
}

.toggle-switch {
  width: 100px;
  height: 50px;
  margin: 6px 0 0 auto;
  position: relative;
}

.switch-label {
  width: 100%;
  height: 50px;
  cursor: pointer;
  border-radius: 25px;
  position: absolute;
}

.checkbox {
  display: none;
  position: absolute;
}

.slider {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  transition: all .3s;
  position: absolute;
}

.slider:before {
  content: "";
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 10px;
  left: 10px;
}

.checkbox:checked ~ .slider:before {
  -webkit-box-shadow: none;
  box-shadow: none;
  transform: translateX(50px);
}

.toggle-switch {
  --light: #d8dbe0;
  --dark: #28292c;
  --link: #1b8170;
  --link-hover: #185e52;
}

.switch-label {
  background-color: var(--dark);
  border: 3px solid var(--dark);
}

.checkbox:checked ~ .slider {
  background-color: var(--light);
}

.slider:before {
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--light);
  box-shadow: inset 12px -4px 0px 0px var(--light);
  background-color: var(--dark);
}

.checkbox:checked ~ .slider:before {
  background-color: var(--dark);
}

.movie-card__name {
